import React, {useEffect, useMemo, useState} from "react";
import {connect} from "react-redux";
import {fetchCurrentClient} from "../../../../redux/requests/Clients";
import {currentClientAC} from "../../../../redux/actions/Clients";
import Loading from "../../../../components/shared-components/Loading";
import {Form, Avatar, Button, Input, DatePicker, Row, Col, message, Upload} from 'antd';
import {UserOutlined} from '@ant-design/icons';
import {ROW_GUTTER} from 'constants/ThemeConstant';
import Flex from 'components/shared-components/Flex'

const Profile = (props) => {
    const {match: {params: {id}}, currentClient} = props
    const [state, setState] = useState(null)

    const avatarUrl = 'data:image/jpeg;base64,'

    useEffect(async () => {
        const data = await fetchCurrentClient(props.fetchCurrentClient, id)
    }, [])

    useMemo(() => {
        setState({
            firstName: currentClient?.name?.split(' ')[0],
            lastName: currentClient?.name?.split(' ')[1],
            email: currentClient?.email,
            username: currentClient?.username,
            phone: currentClient?.phone,
            website: currentClient?.website,
            city: currentClient?.address?.city,
            zipcode: currentClient?.address?.zipcode,
            suite: currentClient?.address?.suite,
            street: currentClient?.address?.street,
        })
    }, [currentClient])

    const onFinish = () => {
        const key = 'updatable';
        message.loading({ content: 'Updating...', key });
        setTimeout(() => {
            setState(state)
            message.success({ content: 'Done!', key, duration: 2 });
        }, 1000)
    }

    return Object.keys(currentClient).length ?
        <>
            <Flex alignItems="center" mobileFlex={false} className="text-center text-md-left" >
                <Avatar size={90} src={avatarUrl} icon={<UserOutlined/>}/>
                <div className="ml-md-3 mt-md-0 mt-3">
                </div>
            </Flex>
            <div className="mt-4">
                <Form
                    name="basicInformation"
                    layout="vertical"
                    initialValues={
                        {
                            'firstName': state?.firstName,
                            'lastName': state?.lastName,
                            'email': state?.email,
                            'username': state?.username,
                            'phoneNumber': state?.phone,
                            'website': state?.website,
                            'address': state?.street,
                            'city': state?.city,
                            'zipcode': state?.zipcode,
                            'suite': state?.suite,
                            'street': state?.street,
                        }
                    }
                    onFinish={onFinish}
                >
                    <Row>
                        <Col xs={24} sm={24} md={24} lg={16}>
                            <Row gutter={ROW_GUTTER}>
                                <Col xs={24} sm={24} md={12}>
                                    <Form.Item
                                        label="First name"
                                        name="firstName"
                                        rules={[
                                            {
                                                required: true,
                                                message: 'Please input your name!',
                                            },
                                        ]}
                                    >
                                        <Input
                                            onChange={(e) => {
                                                setState(prevState => ({...prevState, firstName: e.target.value}))
                                            }}
                                        />
                                    </Form.Item>
                                </Col>
                                <Col xs={24} sm={24} md={12}>
                                    <Form.Item
                                        label="Last name"
                                        name="lastName"
                                        rules={[
                                            {
                                                required: true,
                                                message: 'Please input your username!'
                                            },
                                        ]}
                                    >
                                        <Input
                                            onChange={(e) => {
                                                setState(prevState => ({...prevState, lastName: e.target.value}))
                                            }}
                                        />
                                    </Form.Item>
                                </Col>
                                <Col xs={24} sm={24} md={12}>
                                    <Form.Item
                                        label="Email"
                                        name="email"
                                        rules={[{
                                            required: true,
                                            type: 'email',
                                            message: 'Please enter a valid email!'
                                        }]}
                                    >
                                        <Input
                                            onChange={(e) => {
                                                setState(prevState => ({...prevState, email: e.target.value}))
                                            }}
                                        />
                                    </Form.Item>
                                </Col>
                                <Col xs={24} sm={24} md={12}>
                                    <Form.Item
                                        label="Username"
                                        name="username"
                                        rules={[
                                            {
                                                required: true,
                                                message: 'Please input your username!'
                                            },
                                        ]}
                                    >
                                        <Input
                                            onChange={(e) => {
                                                setState(prevState => ({...prevState, username: e.target.value}))
                                            }}
                                        />
                                    </Form.Item>
                                </Col>
                                <Col xs={24} sm={24} md={12}>
                                    <Form.Item
                                        label="Phone Number"
                                        name="phoneNumber"
                                    >
                                        <Input
                                            onChange={(e) => {
                                                setState(prevState => ({...prevState, phone: e.target.value}))
                                            }}
                                        />
                                    </Form.Item>
                                </Col>
                                <Col xs={24} sm={24} md={12}>
                                    <Form.Item
                                        label="Website"
                                        name="website"
                                    >
                                        <Input
                                            onChange={(e) => {
                                                setState(prevState => ({...prevState, website: e.target.value}))
                                            }}
                                        />
                                    </Form.Item>
                                </Col>
                                <Col xs={24} sm={24} md={24}>
                                    <Form.Item
                                        label="City"
                                        name="city"
                                    >
                                        <Input
                                            onChange={(e) => {
                                                setState(prevState => ({...prevState, city: e.target.value}))
                                            }}
                                        />
                                    </Form.Item>
                                </Col>
                                <Col xs={24} sm={24} md={12}>
                                    <Form.Item
                                        label="Street"
                                        name="street"
                                    >
                                        <Input
                                            onChange={(e) => {
                                                setState(prevState => ({...prevState, street: e.target.value}))
                                            }}
                                        />
                                    </Form.Item>
                                </Col>
                                <Col xs={24} sm={24} md={12}>
                                    <Form.Item
                                        label="Suite"
                                        name="suite"
                                    >
                                        <Input
                                            onChange={(e) => {
                                                setState(prevState => ({...prevState, suite: e.target.value}))
                                            }}
                                        />
                                    </Form.Item>
                                </Col>
                                <Col xs={24} sm={24} md={12}>
                                    <Form.Item
                                        label="Zipcode"
                                        name="zipcode"
                                    >
                                        <Input
                                            onChange={(e) => {
                                                setState(prevState => ({...prevState, zipcode: e.target.value}))
                                            }}
                                        />
                                    </Form.Item>
                                </Col>
                            </Row>

                            <Button type="primary" htmlType="submit">
                                Save Change
                            </Button>
                        </Col>
                    </Row>
                </Form>
            </div>
        </> : <Loading cover="content"/>
}

const mapStateToProps = (state) => {
    return {
        currentClient: state.clients.currentClient
    }
}

const mapDispatchToProps = dispatch => {
    return {
        fetchCurrentClient: clients => dispatch(currentClientAC(clients))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Profile);